body{
    font-family: Montserrat;
}

/* Navbar - container */
.navbar-container{
    display: flex;
    justify-content: space-between;
    margin: 50px;
    padding: 5px 15px;
    border-radius: 12px;
    background: #4a774d;
    font-weight: bold;
    color: white;
    z-index: 1;
    border: 2px solid black;
    position: absolute;
    width: 90%;
}

.nav-mid-list, .nav-right-list{
    /* text-align: center; */
    display: flex;
    gap: 30px;
    list-style: none;
}

.nav-left,
.nav-mid,
.nav-right {
  display: flex;
  align-items: center;
}

.khetify-logo{
    border-radius: 8px;
    box-shadow: 10px 5px px #2e6e4c;
}
/* Navbar done */

/* Carousel CSS */

.overlay-card {
  position: absolute;
  top: 70%;
  left: 10%;
  transform: translateY(-50%);
  z-index: 10;
  padding: 5rem;
  max-width: 700px;
  border-radius: 32px;
  background: rgba(255, 255, 255, 0.30); /* glass effect */
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(50px);
  border: 3px solid black;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
  color: #000;
  text-align: center;
}

/* Heading */
.overlay-card h4 {
  font-weight: 700;
  font-size: 1.8rem;
  margin-bottom: 0.75rem;
}

/* Description */
.overlay-card p {
  font-size: 1.1rem;
  color: #333;
  margin-bottom: 1.5rem;
}

/* Button */
.overlay-card button {
  background-color: #2f8132;
  color: white;
  border: 2px solid #000000;
  padding: 0.6rem 1.5rem;
  font-weight: 600;
  border-radius: 10px;
  transition: all 0.3s ease-in-out;
}

.overlay-card button:hover {
  background-color: white;
  color: #1b5e20;
  border: 2px solid #1b5e20;
  transform: scale(1.05);
}

/* Heading component */
.heading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.heading {
  margin-top: 20px;
  padding: 15px 30px;
  text-align: center;
  border: 2px solid black;
  display: inline-block;
}

.heading-text {
  font-size: 2rem;
  font-weight: 900;
  color: white;
  text-shadow: -1px -1px 0 #000,  
               1px -1px 0 #000,
              -1px 1px 0 #000,
               1px 1px 0 #000;  /* black outline */
  letter-spacing: 2px;
}


.heading-text {
  font-size: 2rem;
  font-weight: 900;
  color: white;
  text-shadow: -1px -1px 0 #000,  
                1px -1px 0 #000,
               -1px 1px 0 #000,
                1px 1px 0 #000;
  letter-spacing: 2px;
}


/* Product component */
.product-body{
  background: url("https://res.cloudinary.com/depyc5ywg/image/upload/v1752842480/Home_page_2_oh1ebu.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: 900px;
  width: 100%;
}

/* Youtube-ad */
.youtube-ad {
  border: 3px solid black;
  border-radius: 12px;
  display: inline-block;
  padding: 8px; /* Add some space between iframe and border */
  background-color: white; /* Optional: to separate from background */
}

.youtube-ad iframe {
  display: block;
  border-radius: 8px;
  border: none;
}


.product-card-container{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

/* Product-Card */
.products-container {
  /* margin: 20px; */
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
  padding: 2rem;
}

.product-card {
  margin-top: 40px;
  margin-left: 70px;
  display: flex;
  align-items: center;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 20px;
  padding: 20px 30px;
  margin-bottom: 20px;
  width: 650px;
  height: auto;
  box-shadow: 0px 10px 20px black;
  position: relative;
  transition: transform 0.2s ease;
}

.product-card:hover {
  transform: translateY(-5px);
}

.product-image {
  width: 100px;
  height: auto;
  margin-right: 20px;
}

.product-content {
  flex: 1;
}

.product-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0 0 10px 0;
}

.product-description {
  font-size: 1rem;
  color: #333;
  line-height: 1.5;
  font-weight: 600;
}

.arrow-button {
  padding-left: 15px;
  font-size: 1.7rem;
  font-weight: bold;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 36px;
  cursor: pointer;
}

